import React from 'react'
import { FaPlayCircle } from 'react-icons/fa';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Button from 'components/Button';
import BackgroundImage from 'components/BackgroundImage';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import Link from 'components/Link';


import tutorialman from './tutorialman.svg';
import Layout from '../Layout'

const steps = ['溝通階段', '溝通解剖學模型', '溝通設計方法及案例']

const datas = [
  {
    title: '設\n定',
    way:['定位', '目標', '策略'],
    step: '前',
    buttontext: '設定',
    links: ['https://url.relab.cc/health_1_1', 'https://url.relab.cc/health_1_2' ]
  },
  {
    title: '互\n動',
    way:['內容', '介面', '形式'],
    step: '中',
    buttontext: '互動',
    links: ['https://url.relab.cc/health_2_1', 'https://url.relab.cc/health_2_2']
  },
  {
    title: '驗\n證',
    way:['認識', '認知', '認同'],
    step: '後',
    buttontext: '驗證',
    links: ['https://url.relab.cc/health_3_1', 'https://url.relab.cc/health_3_2']
  },
]

const Tutorial = () => {
  return (
    <Layout bg="blue" height="100vh" position="relative">
      <FullpageVerticalCenter>
        <Flex px="4.5em" justifyContent="center">
          <Box
            width={2 / 3}
            py="2.25em"
            px={['4em', null, null, null, null, '9.25em']}
            mx="1em"
            bg="white"
            borderRadius="2em"
          >
            <Text fontSize="1.875em" fontWeight="bold" textAlign="center" color="blue">根據結果，特訓你容易忽略的部分</Text>
            <Text fontSize="1.25em" mt="1.125rem">
              前面的九個問題，其實直接對應溝通解剖學模型的九個環節。因此你也可以從作答結果中自己較弱的環節，點選右方按鈕前往了解對應的方法及案例
            </Text>
            <Flex alignItems="center" justifyContent="space-between" pt="1em">
              {steps.map((step, index) => (
                <Box color="blue" position="relative" fontWeight="bold">
                  <Text>{step}</Text>
                  {index !== steps.length - 1 && (
                    <Box
                      position="absolute"
                      height="1px"
                      bg="blue"
                      width="2.25em"
                      right="0"
                      top="50%"
                      transform="translate(120%, -50%)"
                    >
                      <Box
                        position="absolute"
                        left="96%"
                        top="50%"
                        transform="translateY(-52%)"
                      >●</Box>
                    </Box>
                  )}
                </Box>
              ))}
            </Flex>
            <Box>
              {datas.map(({ title, way, step, buttontext, links }, index) => (
                <Flex
                  key={index}
                  alignItems="center"
                  py="0.5em"
                  justifyContent="space-between"
                  borderTop={index && '1px solid'}
                >
                  <Box fontSize="1.625em">
                    溝通{step}
                  </Box>
                  <Flex alignItems="center">
                    <Box textAlign="center">
                      <Box
                        py="1.75rem"
                        px="0.5rem"
                        bg="blue"
                        color="white"
                        whiteSpace="pre-wrap"
                        borderRadius="1em"
                        fontSize="1.125em"
                        fontWeight="bold"
                        lineHeight="1.25"
                        position="relative"
                      >
                        {title}
                      </Box>
                    </Box>
                    <Box textAlign="center" mx="1em">
                      {way.map((w, i) => (
                        <Box key={i}>
                          <Box fontSize="0.875em" fontWeight="bold" color="blue">
                            {w}
                          </Box>
                          {i !== way.length - 1 && <Box lineHeight="1.25">|</Box>}
                        </Box>
                      ))}
                    </Box>
                  </Flex>
                  <Box textAlign="center">
                    {links.map((link, k) => (
                      <Box my="0.5em">
                        <Button
                          rightIcon={FaPlayCircle}
                          px="0.75em"
                          py="0.5em"
                          borderRadius="1.5em"
                          is={Link}
                          href={link}
                        >
                        {buttontext}{k ? '相關案例' : '方法說明'}
                      </Button>
                    </Box>
                    ))}
                  </Box>
                </Flex>
              ))}
            </Box>
          </Box>
          <Flex flexDirection="column" justifyContent="space-between" width={1 / 3} py="2.25em" px="2.875em" mx="1em" bg="white" borderRadius="2em">
            <Box>
              <Text fontSize="1.875em" fontWeight="bold" textAlign="center" color="blue">全面強化衛教溝通</Text>
              <Text mt="1.25em" fontSize="1.5em">
                你可以前往衛教溝通設計指南，從頭開始瞭解並溝通設計的方法，系統性地為你的溝通策略找出解決方案！
              </Text>
            </Box>
            <Box textAlign="center" whiteSpace="pre-wrap" px="20%" mb="2em">
              <Button
                is={Link}
                rightIcon={FaPlayCircle}
                px="1.25em"
                py="3em"
                lineHeight="1.5"
                textAlign="left"
                borderRadius="1.5em"
                href="https://url.relab.cc/health_all"
              >{'前往衛教溝通\n設計指南'}</Button>
            </Box>
          </Flex>
        </Flex>
      </FullpageVerticalCenter>
      <Box position="absolute" bottom="0" left="1em" width="11%">
        <BackgroundImage src={tutorialman} ratio={404.33 / 162.13} />
      </Box>
      <Box position="absolute" bottom="2em" right="0.5em" whiteSpace="pre-wrap">
        <Button.circle is={Link} to="/">{'再次\n測驗'}</Button.circle>
      </Box>
    </Layout>
  )
}

export default Tutorial
